
import CardHeader from './Card-Header'
import CardItem from './Card-Item'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


const waitCard = [{
id: 1,
price: 5,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '未使用'
}, {
id: 2,
price: 15,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '未使用'
}, {
id: 3,
price: 30,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '未使用'
}]

const usedCard = [{
id: 4,
price: 5,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已使用'
}, {
id: 5,
price: 20,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已使用'
},  {
id: 6,
price: 20,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已使用'
}, {
id: 7,
price: 35,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已使用'
}]

const expireCard = [{
price: 4,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已过期'
}, {
price: 3,
name: '优惠券',
limit: '仅限满减使用',
serviceTime: '2017.05.15~2017.08.23',
status: '已过期'
}]

export default {
name: 'card',
components: {
CardHeader,
Flexbox,
FlexboxItem,
CardItem,
Toast
},
created: function() {
console.log('创建');
},
data () {
return {
msg: 'MyCard',
token: '',
userId: '',
cards: waitCard,
isActive: [true,false,false]
}
},
methods: {
getCard (index) {
if (index ===0) {
this.cards = waitCard
this.isActive[0] = true
this.isActive[1] = false
this.isActive[2] = false
console.log('usedCard')
} else if (index ===1) {
this.cards = usedCard
this.isActive[0] = false
this.isActive[1] = true
this.isActive[2] = false
console.log('waitCard')
} else {
this.cards = expireCard
this.isActive[0] = false
this.isActive[1] = false
this.isActive[2] = true
console.log('expireCard')
}
},
toast (txt) {
this.$vux.toast.show({
text: txt,
type: 'text',
onShow () {
console.log('show')
}
})
}
},
watch: {
token: function (newToken) {
this.msg = 'gaib'
}
},
mounted: function () {
window.android.startFunction()
setTimeout(() => {
if (sessionStorage.userId) {
this.toast(sessionStorage.userId)
}
}, 1000);
}

}




