

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import App from './App'
import Home from './components/mall/Home.vue'
import MyCard from './components/card/MyCard'
import MyExchange from './components/exchange/MyExchange'
import SubmitOrder from './components/order/SubmitOrder'
import Hello from './components/Hello'
import MerchandiseDetails from './components/detail/MerchandiseDetails'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)

Vue.use(VueRouter)
Vue.use(VueResource)

const routes = [
{
path: '/',
component: Home
},
{
path: '/MyCard',
component: MyCard,
meta: { title: '我的卡券' }
},
{
path: '/Hello',
component: resolve => System.import(Hello),
meta: { title: '网上商城' }
},
{
path: '/MyExchange',
component: MyExchange,
},
{
path: '/MerchandiseDetails',
component: MerchandiseDetails,
},
{
path: '/SubmitOrder',
component: SubmitOrder
},

]

const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



