
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: 'mall-list',
components: {
Grid,
GridItem,
AjaxPlugin,
Toast
},
data() {
return {
dataSource: [







],
errorText: "",
}
},
created: function() {
localStorage.clear();
console.log('created');
if (sessionStorage.list) {
console.log(sessionStorage.list);
this.dataSource = JSON.parse(sessionStorage.list);
} else {
if (sessionStorage.userID && sessionStorage.token) {
console.log('created1');
this.fetchListData();
} else {
setTimeout(() => {
console.log('created2');
this.fetchListData();
}, 1000);
}
}










},
methods: {
fetchListData: function() {
this.$http({
method: 'GET',
url: "http://192.168.10.93:8080/iznaer/integrationMall/findGoodsList.do",
params: {
userId: sessionStorage.userID,
token: sessionStorage.token,
},
dataType: 'jsonp'
}).then((response) => {
if (response.body.code == 200) {
this.dataSource = response.data.results;
console.log(this.dataSource);
var list = JSON.stringify(this.dataSource);
sessionStorage.list = list;
if (this.dataSource.length == 0) { console.log('空数据') }
} else {
if (response.body.description) {
console.log(response.body.description)
}
}
}).catch((error) => {
console.log(error)
})
},




go: function(name, event) {







console.log(name);
if (event.target.className == "item-exchange") {
console.log('兑换');
this.$router.push({path: '/SubmitOrder'});
} else {
console.log('详情');
this.$router.push({path: '/MerchandiseDetails'});
}






},
}

}



