	export default {
		name: 'mall-header',
		components: {

		},
		data() {
			return {
				nickName: sessionStorage.nickName,
				iconPath: sessionStorage.iconPath,
			}
		},
		methods: {
			myCardList: function() {
				this.$router.push('/MyCard');
				//toMyCardList();
			},
			myCardExchangeRecord: function() {
				console.log('跳转兑换记录');
				this.$router.push('/MyExchange');
			}
		},
		mounted: function() {

			setTimeout(() => {
				if (sessionStorage.nickName) {
					console.log(sessionStorage.nickName);
				}
				if (sessionStorage.iconPath) {
					console.log(sessionStorage.iconPath);	
				}
				this.nickName = sessionStorage.nickName;
				this.iconPath = sessionStorage.iconPath;
			}, 1000);
			
		},
		watch: {
    	nickName(newValue, oldValue) {
        console.log(newValue)
    }
}


	}



