
import ExchangeItem from './Exchange-Item'
import Toast from 'vux/src/components/toast/index.vue'


const expireCard = [{
id: 123,
price: 4,
name: '优惠券',
limit: '仅限满减使用',
exchangeTime: '2017.05.15~2017.08.23',
status: '未发货'
}, {
id: 456,
price: 3,
name: '优惠券',
limit: '仅限满减使用',
exchangeTime: '2017.05.15~2017.08.23',
status: '已发货'
}]

export default {
name: 'exchange',
components: {
ExchangeItem,
Toast
},
data () {
return {
msg: 'Activity',
token: '',
userId: '',
cards: expireCard
}
},
methods: {
toast (txt) {
this.$vux.toast.show({
text: txt,
type: 'text',
onShow () {
console.log('show')
}
})
}
}

}




